#launcher {
  width: 250px;
  height: 270px;
  background-color: #fff;
  border-style: solid;
  margin: 50px auto;
}

#push {
  width: 250px;
  height: auto;
  margin: 5px auto;
}

#dado {
  width: 200px;
  height: 200px;
  background-color: #fff;
  border-radius: 5%;
  border-color: #333;
  border-width: 4px;
  border-style: solid;
  margin: 5% auto;
}

.point {
  width: 17.5%;
  height: 17.5%;
  background-color: #000;
  border-radius: 50%;
  position: relative;
  top: 0;
  left: 0;
}

.hide {
  background-color: #fff;
}

